body {
  margin: 0px;
}
.menuIcon {
  width: 1.6em;
  height: 1.6em;
}
:root {
  --borRad: 10px;
  --fontHeader: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.header {
  background: #568051;
  color: white;
}

.navbar {
  height: 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-inline-end: 2rem;
}

.navbar .brand {
  display: flex;
  align-items: center;
  font-size: 22px;
  font-family: var(--fontHeader);
  order: 2;
}

.navbar .brand img {
  width: 45px;
}

.navbar .toggle {
  width: 4em;
  height: 4em;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar .toggle:active {
  background-color: #40623c;
}

.navbar .toggle .toggle--icon {
  width: 1.6em;
}

.search--bar {
  display: none;
}

.navbar--actions {
  display: none;
}

@media (min-width: 48em) {
  .navbar--actions {
    display: none;
  }
}

@media (min-width: 768px) {
  .navbar {
    margin-inline-start: 4rem;
    margin-inline-end: 4rem;
    display: flex;
    justify-content: space-between;
  }

  .navbar .brand {
    order: -1;
    margin-block: 0;
  }

  .navbar .toggle {
    display: none;
  }

  .search--bar {
    display: flex;
  }

  .search--input {
    border: 2px solid #40623c;
    border-radius: var(--borRad) 0 0 var(--borRad);
    border-right: none;
    width: 20em;
    font-size: 18px;
    font-family: var(--fontHeader);
  }

  .search--button {
    border: 2px solid #40623c;
    border-radius: 0 var(--borRad) var(--borRad) 0;
    border-left: none;
  }

  .search--button:hover,
  .search--button:active {
    background: #40623c;
    cursor: pointer;
  }

  .search--input,
  .search--button { 
    padding: 0.5em;
  }

  .search--button img {
    width: 1rem;
  }

  @media (min-width: 1024px) {
    .navbar--actions {
      display: none;
    }  
  }
  
  @media (min-width: 1024px) {
    .navbar--actions {
      display: flex;
      align-items: center;
      gap: 1rem;
      font-size: 22px;
      font-family: var(--fontHeader);
    }

    .navbar--actions dl {
      margin: 0;
    }
  }
}
